/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

body {
  background: #FFF;
  font-family: 'Ubuntu', sans-serif;
  line-height: 1.4;
  color: #252426;
  background-color: #f2f2f2;
  position: relative;
  -webkit-overflow-scrolling: touch;
}

body.is-fixed {
  overflow: hidden;
  max-height: 100vh;
}

.btn {
  border: 0;
  outline: 0;
  display: inline-block;
  position: relative;
  overflow: hidden;
  font-size: 14px;
  font-family: 'Ubuntu', sans-serif;
}

.btn_ts {
  background-color: transparent;
}

.btn_link {
  text-decoration: none;
}

.btn_yellow {
  background-color: #f6eb06;
  border-radius: 4px;
  color: #3f3f41;
  padding: 15px 25px;
}

.gradient-bg {
  background: linear-gradient(to bottom, #F3F9A7 50%, #fff 100%);
}

.gradient-bg-reverse {
  background: linear-gradient(to bottom, #fff 50%, #F3F9A7 100%);
}

.section {
  position: relative;
}

.section__in {
  padding: 50px 0;
  background-repeat: repeat-y;
  background-position: 0 50%;
  background-size: contain;
}

.section__title {
  text-align: center;
  margin-bottom: 40px;
  color: #150d3f;
  font-size: 18px;
}

.section__title_white {
  color: #f2f2f2;
}

.section__title_white h2 {
  font-weight: normal;
}

.full-l {
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover;
}

.l {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 50px;
}

.out {
  margin-bottom: 300px;
  box-shadow: 0 6px 30px rgba(0, 0, 0, 0.3);
  position: relative;
  background-repeat: repeat-y;
  background-position: 0 50%;
  background-size: contain;
}

.out:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #f2f2f2;
}

.in {
  position: relative;
  z-index: 1;
}

.parallax {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: repeat-y;
  background-position: 0% 50%;
  background-size: contain;
  z-index: 0;
}

.start {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: transparent;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.start__in {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 80%;
  text-align: center;
}

.start__title {
  font-size: 48px;
  color: #150d3f;
  padding: 10vh 0;
}

.start__title_white {
  color: #f2f2f2;
}

.start__btn {
  text-align: center;
}

.start__btn .btn {
  animation: scroll infinite 2s ease-out;
}

.start__btn .btn:hover {
  animation-play-state: paused;
}

.start__btn .icon-arrow {
  font-size: 72px;
  fill: #f2f2f2;
}

.header {
  background: linear-gradient(to right, #41295a, #2f0743);
  padding: 0 20px;
  height: 70px;
  display: flex;
}

.header.is-fixed {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  width: 100%;
  transform: translate(0, -100%);
}

.header.is-fixed.is-animated {
  transform: translate(0, 0);
  transition: transform .4s ease-in-out;
}

.header__logo {
  width: 25%;
  margin: auto 0;
}

.header__logo img {
  height: 42px;
  display: block;
}

.header__nav {
  width: 75%;
  position: relative;
}

.header__icon {
  display: none;
  height: 100%;
  position: relative;
}

@media only screen and (max-width: 1023px) {
  .header__icon {
    display: block;
  }
}

.nav {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
}

@media only screen and (max-width: 1023px) {
  .nav {
    display: none;
  }
  .nav.is-active {
    display: block;
    height: auto;
    width: 100vw;
    right: -20px;
    position: absolute;
    top: 70px;
    background-color: rgba(21, 13, 63, 0.9);
    text-align: center;
  }
}

.nav__item {
  position: relative;
  overflow: hidden;
}

.nav__item a {
  text-decoration: none;
  font-weight: 500;
  letter-spacing: 0.4px;
  font-size: 18px;
  color: #f2f2f2;
  display: block;
  height: 100%;
  padding: 0 20px;
  line-height: 70px;
  transition: all 0.2s ease;
}

.nav__item a:hover {
  opacity: .7;
}

.nav-toggle {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(0, -50%);
  width: 40px;
  height: 28px;
}

.nav-toggle span {
  display: block;
  width: 100%;
  height: 4px;
  border-radius: 6px;
  transition: all 0.2s ease;
  position: absolute;
  background-color: rgba(242, 242, 242, 0.8);
}

.nav-toggle span:nth-child(1) {
  top: 0;
  transform-origin: left center;
}

.nav-toggle span:nth-child(2) {
  top: 12px;
  transform-origin: left center;
}

.nav-toggle span:nth-child(3) {
  top: 24px;
  transform-origin: left center;
}

.nav-toggle.is-open span:nth-child(1) {
  transform: rotate(45deg);
  top: -2px;
}

.nav-toggle.is-open span:nth-child(2) {
  width: 0;
  opacity: 0;
}

.nav-toggle.is-open span:nth-child(3) {
  transform: rotate(-45deg);
  top: 26px;
}

.bms__blocks {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.bms__block {
  width: 24%;
}

.bms__block .block {
  height: 100%;
}

.bms__logo {
  width: 90%;
  min-height: 50px;
  margin: 0 auto;
  position: relative;
}

.bms__logo img {
  max-width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: block;
}

.bms h3 {
  margin-bottom: 10px;
}

.adv {
  background: linear-gradient(165deg, #1d2b64, #f8cdda);
}

.adv__in {
  position: relative;
}

.adv__arr {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  width: 35px;
  height: 35px;
  z-index: 100;
  background-color: transparent;
  transition: all 0.2s ease;
}

.adv__arr .icon-arrow {
  font-size: 48px;
  fill: #f2f2f2;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.adv__arr_prev {
  left: -35px;
}

.adv__arr_prev .icon-arrow {
  transform: translate(-50%, -50%) rotate(90deg);
}

.adv__arr_prev:hover {
  transform: translate(-4px, -50%);
}

.adv__arr_next {
  right: -35px;
}

.adv__arr_next .icon-arrow {
  transform: translate(-50%, -50%) rotate(-90deg);
}

.adv__arr_next:hover {
  transform: translate(4px, -50%);
}

.adv__slider {
  background-color: #fff;
}

.adv__slide {
  font-size: 0;
}

.adv__chart, .adv__list {
  display: inline-block;
  vertical-align: top;
}

.adv__chart {
  width: 35%;
  padding: 20px;
  position: relative;
}

.adv__list {
  width: 30%;
  min-height: 460px;
  font-size: 16px;
  padding: 30px 20px;
  background: #fff;
  box-shadow: inset 0px 1px 29px -5px rgba(0, 0, 0, 0.61);
  text-align: center;
  font-size: 0;
}

.adv__list:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.adv__list-in {
  display: inline-block;
  vertical-align: middle;
  text-align: left;
}

.adv__list h4 {
