  font-size: 16px;
}

.adv__list ul li:before {
  content: "";
  display: block;
  position: absolute;
  top: 5px;
  left: 0;
  width: 12px;
  height: 12px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  background-image: url("data:image/svg+xml;base64,");
}

.pie svg {
  display: block;
  overflow: hidden;
  width: 80%;
