.block_simple {
  border: 0;
}

.block_shadow {
  transition: 0.2s 0.2s ease;
}

.block_shadow:hover {
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  transform: translateY(-8px);
}

.block__title {
  padding: 20px 30px;
  text-align: center;
  color: #150d3f;
  border-bottom: 1px solid #eceef5;
}

.block__title h3 {
  font-weight: normal;
  text-transform: uppercase;
  font-size: 20px;
}

.block__in {
  padding: 20px 30px;
}

.contacts .block {
  height: 100%;
  position: relative;
}

.contacts .block .btn_yellow {
  margin-top: 45px;
}

.contacts__in {
  display: flex;
  justify-content: space-between;
}

.contacts__item {
  width: 32%;
  text-align: center;
}

.contacts__item h3 {
  margin-bottom: 15px;
}

.contacts__item p {
  margin-bottom: 30px;
}

.contacts__icon {
  margin-bottom: 20px;
}

.contacts__tel {
  font-size: 16px;
  font-weight: 600;
  color: #150d3f;
  text-decoration: none;
  display: block;
  margin-bottom: 15px;
}

.contacts__social li {
  display: inline-block;
  vertical-align: middle;
}

.contacts__social li a {
  display: block;
}

.contacts__social .icon {
  font-size: 40px;
  fill: #150d3f;
  transition: all 0.2s ease;
}

.contacts__social .icon:hover {
  opacity: .8;
}

.clients {
  padding-bottom: 50px;
}

.clients__in {
  position: relative;
}

.clients__arr {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  width: 35px;
  height: 35px;
  z-index: 100;
  background-color: transparent;
  transition: all 0.2s ease;
}

.clients__arr .icon-arrow {
  font-size: 48px;
  fill: #f2f2f2;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.clients__arr_prev {
  left: -35px;
}

.clients__arr_prev .icon-arrow {
  transform: translate(-50%, -50%) rotate(90deg);
}

.clients__arr_prev:hover {
  transform: translate(-4px, -50%);
}

.clients__arr_next {
  right: -35px;
}

.clients__arr_next .icon-arrow {
  transform: translate(-50%, -50%) rotate(-90deg);
}

.clients__arr_next:hover {
  transform: translate(4px, -50%);
}

.clients__item {
  width: 150px;
  padding: 15px 0;
}

.circle {
  width: 150px;
  height: 150px;
  margin: 0 auto;
  background-color: #fff;
  border-radius: 50%;
  border: 1px solid #eceef5;
  box-shadow: 0 8px 30px rgba(0, 0, 0, 0.1);
  transition: 0.4s 0.2s ease;
  text-align: center;
  font-size: 0;
  position: relative;
  text-decoration: none;
  display: block;
  color: #150d3f;
}

.circle:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.circle:hover {
  transform: rotate3d(0, 2, 0, 180deg);
}

.circle:hover .circle__logo {
  opacity: 0;
}

.circle:hover .circle__tooltip {
  opacity: 1;
  color: rgba(21, 13, 63, 0.7);
}

.circle__in {
  font-size: 14px;
  display: inline-block;
  vertical-align: middle;
}

.circle__logo {
  width: 125px;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  transition: 0.3s 0.2s ease;
}

.circle__logo img {
  max-width: 80%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.circle__tooltip {
  width: 125px;
  padding: 15px 0 10px;
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  font-weight: 600;
  transition: 0.3s 0.2s ease;
  opacity: 0;
  transform: rotate3d(0, 2, 0, 180deg);
}

.circle__tooltip .btn {
  padding: 5px;
  line-height: 1.5;
  font-size: 16px;
  font-weight: 600;
}

.form {
  min-width: 100%;
}

.form .block__title {
  padding: 20px 50px;
}

.form .block__title h3 {
  font-weight: 600;
}

.form__in {
  padding: 20px 50px;
}

.form .btn {
  display: block;
  margin: 0 auto;
}

.input {
  position: relative;
  font-size: 14px;
  margin-bottom: 15px;
}

.input input {
  width: 100%;
  height: 44px;
  border: 1px solid #d5d3ff;
  border-radius: 2px;
  padding: 16px 20px 0;
}

.input input:focus + label {
  font-size: 10px;
  transform: translateY(-10px);
}

.input label {
  position: absolute;
  top: 14px;
  left: 20px;
  transition: all 0.2s ease;
}

.input textarea {
  width: 100%;
  height: 120px;
  padding: 20px 20px 0;
  border: 1px solid #d5d3ff;
  border-radius: 2px;
  resize: none;
}

.input textarea:focus + label {
  font-size: 10px;
  transform: translateY(-10px);
}

.footer {
  background: linear-gradient(to right, #41295a, #2f0743);
  padding: 50px 0 30px;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: -9999;
  min-height: 300px;
}

.footer__logo {
  max-width: 30%;
  margin: 0 auto 50px;
}

.footer__logo img {
  width: 100%;
}

.footer__nav {
  text-align: center;
  margin-bottom: 50px;
}

.footer__nav li {
  display: inline-block;
  vertical-align: middle;
  margin: 0 10px;
}

.footer__nav li a {
  color: rgba(242, 242, 242, 0.8);
  text-decoration: none;
  font-size: 17px;
  letter-spacing: 0.4px;
  transition: all 0.2s ease;
}

.footer__nav li a:hover {
  opacity: .7;
}

.footer__copyright {
  color: rgba(242, 242, 242, 0.2);
  font-size: 14px;
  letter-spacing: 0.2px;
  text-align: center;
}

.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 2000;
  display: none;
  text-align: center;
  font-size: 0;
}

.popup:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

.popup__close {
  display: block;
  position: absolute;
  right: -40px;
  top: -40px;
}

.popup__close .icon-close {
  font-size: 36px;
  fill: #fff;
}

.popup__overlay {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.75);
}

.popup__box {
  position: relative;
  width: 70%;
  max-width: 700px;
  z-index: 2;
  display: inline-block;
  vertical-align: middle;
}
