.contacts {
	.block {
		height: 100%;
		position: relative;
		.btn_yellow {
			margin-top: 45px; } }
	&__in {
		display: flex;
		justify-content: space-between; }
	&__item {
		width: 32%;
		text-align: center;
		h3 {
			margin-bottom: 15px; }
		p {
			margin-bottom: 30px; } }
	&__icon {
		margin-bottom: 20px; }

	&__tel {
		font-size: 16px;
		font-weight: 600;
		color: $violet;
		text-decoration: none;
		display: block;
		margin-bottom: 15px; }
	&__social {
		li {
			display: inline-block;
			vertical-align: middle;
			a {
				display: block; } }
		.icon {
			font-size: 40px;
			fill: $violet;
			@include trans;
			&:hover {
				opacity: .8; } } } }
