.bms {
	&__blocks {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap; }
	&__block {
		width: 24%;
		.block {
			height: 100%; } }
	&__logo {
		width: 90%;
		min-height: 50px;
		margin: 0 auto;
		position: relative;
		img {
			max-width: 100%;
			@include vcenter;
			display: block; } }
	h3 {
		margin-bottom: 10px; } }


