.popup {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 2000;
    display: none;
    @include vertical;
    &__close {
        display: block;
        position: absolute;
        right: -40px;
        top: -40px;
        .icon-close {
            font-size: 36px;
            fill: #fff; } }
    &__overlay {
        position: absolute;
        top: 0;
        left: 0;
        z-index: 1;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.75); }
    &__box {
        position: relative;
        width: 70%;
        max-width: 700px;
        // top: 50%
        // left: 50%
        z-index: 2;
        display: inline-block;
        vertical-align: middle;

 } }        // transform: translate(-50%, -50%)

