.work {
	background: linear-gradient(165deg, #5f2c82, #49a09d);
	&__list {
		padding-top: 20px; } }

.timeline {
	padding-top: 100px;
	&__item {
		&-in {
			max-width: 80%;
			margin: 0 auto;
			text-align: center; }
		h3 {
			color: $white;
			font-weight: normal;
			font-size: 24px;
			letter-spacing: 0.8px;
			margin-bottom: 20px; }
		p {
			color: transparentize($white, .3);
			letter-spacing: 0.5px;
			font-size: 17px; } }
	&__icon {
		width: 100px;
		margin: 0 auto 30px;
		img {
			max-width: 100%; } }
	.slick-dots {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translate(-50%, 0);
		display: flex;
		width: 90%;
		justify-content: space-between;
		li {
			width: 100/5 + %;
			position: relative;
			&.slick-active {
				button {
					color: $white;
					font-weight: 600;
					border-color: $white;
					&:after {
						opacity: 1; } } }


			&:before {
				content: "";
				display: block;
				width: calc(100% - 46px);
				height: 1px;
				background-color: transparentize($white, .7);
				position: absolute;
				top: 50%;
				right: 0px;
				transform: translate(0, -50%);
				z-index: 0;
				opacity: .8; }
			&:last-child {
				width: auto; }

			button {
				display: inline-block;
				width: 46px;
				height: 46px;
				border-radius: 50%;
				position: relative;
				background-color: transparent;
				font-size: 20px;
				line-height: 44px;
				border: 1px solid transparentize($white, .7);
				color: transparentize($white, .7);

				// &:after
