.adv {
	background: linear-gradient(165deg, #1d2b64, #f8cdda);
	&__in {
		position: relative; }
	&__arr {
		position: absolute;
		top: 50%;
		transform: translate(0, -50%);
		width: 35px;
		height: 35px;
		z-index: 100;
		background-color: transparent;
		@include trans;
		.icon-arrow {
			font-size: 48px;
			fill: $white;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%); }

		&_prev {
			left: -35px;
			.icon-arrow {
				transform: translate(-50%, -50%) rotate(90deg); }
			&:hover {
				transform: translate(-4px, -50%); } }
		&_next {
			right: -35px;
			.icon-arrow {
				transform: translate(-50%, -50%) rotate(-90deg); }
			&:hover {
				transform: translate(4px, -50%); } } }
	&__slider {
		background-color: #fff; }
	&__slide {
		font-size: 0; }
	&__chart, &__list {
		display: inline-block;
		vertical-align: top; }
	&__chart {
		width: 35%;
		padding: 20px;
		position: relative; }
	&__list {
		width: 30%;
		min-height: 460px;
		font-size: 16px;
		padding: 30px 20px;
		background: #fff;
		box-shadow: inset 0px 1px 29px -5px rgba(0,0,0,0.61);
		@include vertical;
		&-in {
			display: inline-block;
			vertical-align: middle;
			text-align: left; }

		h4 {
			font-size: 24px;
			font-weight: normal;
			margin-bottom: 30px;
			color: $violet; }
		ul {
			color: $violet;
			li {
				position: relative;
				padding-left: 25px;
				margin-bottom: 20px;
				font-size: 16px;
				&:before {
					content: "";
					display: block;
					position: absolute;
					top: 5px;
					left: 0;
					width: 12px;
					height: 12px;
					@include cover;
					background-image: url('data:image/svg+xml;base64,'); } } } } }



